.Proyectosdiv{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #1e404f;
    height: 100vh;
}
.containerProyects {
    height: 74vh;
    width: 80vw;
    background-image: url("../imagenes/fondoJS.jpg");
    background-size: cover;
    background-position: center;
    border-radius: 10px;
    box-shadow: 0px 4px 10px 6px rgba(0, 0, 0, 0.5);
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr 1fr;
    place-items: center;
    
}
.proyecto1{
    height: 25vh;
    width: 20vw;
    background-color: rgba(237, 231, 231, 0.3);
    border-radius: 5px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(5px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: width 0.3s ease, height 0.3s ease ;
}
.contenido {
    width: 96%;
    height: 80%;
    border-radius: 5px;
}
.miniNav p {
    height: 3vh;
    margin: 0;
    font-weight: bold;
    margin-bottom: 0.6vh;
}
.img1 {
    height: 100%;
    width: 100%;
    border-radius: 5px;
}
.proyecto1:hover{
    width: 21vw;
    height: 27vh;
    cursor: pointer;
    transition: width 0.3s ease-in-out, height 0.3s ease-in-out, box-shadow 0.3s ease ;
    box-shadow: 0px 4px 10px 6px rgba(255, 255, 255, 0.164);
}
.openVentana {
    height: 70vh;
    width: 60vw;
    background-color: rgba(237, 231, 231, 0.3);
    border-radius: 5px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(5px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: width 0.3s ease, height 0.3s ease ;
    position: absolute;
    z-index: 10;
}
.openVentana .miniNav{
    height: 3vh;
    margin: 0;
    font-weight: bold;
    margin: 0;
    width: 99%;
    height: 8%;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.openVentana .miniNav p {
    margin-left: 5px;
}
.close {
    height: 4vh;
    width: 4vw;
    border-radius: 5px;
    transition: background-color 0.3s ease, color 0.3s ease, box-shadow 0.3s ease;
}
.close:hover {
    background-color: rgb(198, 46, 46);
    box-shadow: 0px 0px 5px 4px rgba(243, 2, 2, 0.507);
    color: white;
    transition: background-color 0.3s ease, color 0.3s ease, box-shadow 0.3s ease;
}
.openVentana .contenido {
    background-color: rgb(32, 32, 32);
    border: 1px solid black;
    width: 98.5%;
    height: 90%;
    border-radius: 5px;
    margin: 0 0 6px 0 ;
    display: grid;
    grid-template-columns: 1.4fr 1fr;
    grid-template-rows: 3fr 1fr;
}
.openVentana .contenido .divImgOpen {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.imgPrincipal{
    width: 93%;
    height: 90%;
    border-radius: 5px;
}
.openVentana .contenido .descripcion{
    margin: 0 30px 0 30px;
    text-align: start;
    font-size: 1.2em;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
}
.descripcion h2 {
    text-align: center;
}
.logos {
    height: 16vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
.img {
    height: 10vh;
    width: auto;
}
.contButtons {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.contButtons button {
    height: 6vh;
    width: 8vw;
    border-radius: 20px;
    font-size: 1em;
    background-color: rgb(117, 118, 117);
    box-shadow: 0px 0px 5px 4px rgba(53, 52, 52, 0.507);
    border: none;
}
.contButtons button:hover {
    cursor: pointer;
    background-color: rgb(77, 75, 75);
    box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.507);
    color: white;
    transition: 0.3s ease;
}
